import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import {
  SceneLink as SceneLinkModel,
  Scene as SceneModel,
} from '../models/Elements'

interface SceneLinkContainerProps {
  top: number
  left: number
  width: number
  height: number
}

const SceneLinkContainer = styled.div<SceneLinkContainerProps>`
  width: ${(props) => props.width}px;
  height: ${(props) => props.height}px;
  top: ${(props) => props.top}px;
  left: ${(props) => props.left}px;
  position: absolute;
  cursor: pointer;
`

interface SceneLinkProps {
  scene: SceneModel
  action: SceneLinkModel
  changeToScene: (sceneUid: number) => void
}

function SceneLink({ scene, action, changeToScene }: SceneLinkProps) {
  return (
    <SceneLinkContainer
      top={action.y}
      left={action.x}
      width={action.actWidth}
      height={action.actHeight}
      onClick={() => {
        changeToScene(Number(action.toScene))
      }}
    />
  )
}

export default SceneLink
