export interface Action {
  uid: string
  x: number
  y: number
  actionType: string
  actWidth: number
  actHeight: number
}

export interface Card extends Action {
  doReceiveCard: boolean
  doEndWithLink: boolean
  sceneLink: number
  requiredCard: string | number
  initialText: string
  advancingText: string
  title: string
}

export interface SceneLink extends Action {
  toScene: number
}

export interface Scene {
  sceneName: string
  uid: number
  backgroundPath: string
  actions: Array<Action>
  cards: Array<Card>
  sceneLinks: Array<SceneLink>
}

export interface MetaScene {
  headerImage: string
  description: string
  title: string
}

export const SceneUtils: any = {
  of: (conf: { scenes: Array<Scene> }) => {
    const scenes: Array<Scene> = []
    for (let i = 0; i < conf.scenes.length; i++) {
      scenes.push(conf.scenes[i])
      const cards: Array<Card> = []
      const sceneLinks: Array<SceneLink> = []

      for (let j = 0; j < conf.scenes[i].actions.length; j++) {
        const action: Action = conf.scenes[i].actions[j]
        switch (action.actionType) {
          case 'card': {
            const cardAction = action as Action & { properties: Card }
            cards.push({
              ...action,
              ...cardAction.properties,
            } as Card)
            break
          }
          case 'scenelink': {
            sceneLinks.push(action as SceneLink)
            break
          }
        }
      }
      scenes[i].cards = cards
      scenes[i].sceneLinks = sceneLinks
    }
    console.log(scenes)
    return scenes
  },
}
