import { createGlobalStyle, css } from 'styled-components'

const GlobalStyle = createGlobalStyle`
  head, body {
    overflow:hidden;
  }
  a {
    text-decoration: none;
    color: black;
    border-bottom: #343a40 0.125em solid;
  }
  .no-hover  {
    pointer-events: none;
  }

  #root {
    ${
      Object.hasOwn(process.env, 'REACT_APP_WHOLE_SCREEN') &&
      !!process.env.REACT_APP_WHOLE_SCREEN &&
      css`
        height: 100vh;
      `
    }
    display: flex;
    justify-content: center;
    align-items: center;
  }
  `

export default GlobalStyle
