import React, {
  ChangeEvent,
  MouseEventHandler,
  useEffect,
  useState,
} from 'react'
import styled from 'styled-components'
import {
  Scene as SceneModel,
  Card as CardModel,
  MetaScene as MetaSceneModel,
} from '../models/Elements'
import Card from './Card'
import SceneLink from './SceneLink'

interface StartSceneProps {
  headerImage: string
  title: string
  startText: string
  doDisplay: boolean
  startGame: () => void
}

interface SceneContainerProps {
  _display: string
}

interface HeaderProps {
  headerImage: string
}

const SceneContainer = styled.div<SceneContainerProps>`
  margin: 0 auto;
  height: 100%;
  display: ${(props) => props._display};
  width: inherit;
  z-index: 10;
  grid-template-rows: 1fr 2fr 1fr;
`

const Header = styled.div<HeaderProps>`
  background-image: url(${(props) => props.headerImage});
  background-size: contain;
  background-repeat: no-repeat;
`

const StartButton = styled.a`
  color: white;
  text-transform: uppercase;
  border-radius: 3px;
  letter-spacing: 1px;
  text-decoration: none;
  cursor: pointer;
  background-color: black;
  font-weight: 700;
  padding: 0px 60px;
  font-size: 36pt;
  margin: 10px 0px;
  font-family: 'Open Sans';
  text-align: center;
  &:hover {
    background-color: rgb(65, 65, 65);
  }
`

const Center = styled.div``

const Footer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`

const TextContainer = styled.div``

const Title = styled.p`
  font-family: 'Open Sans';
  font-size: 28pt;
  margin-bottom: 10px;
`

const DescriptionText = styled.p`
  font-family: 'Open Sans';
  font-size: 20pt;
  margin-top: 0px;
`

function StartScene({
  headerImage,
  title,
  startText,
  doDisplay,
  startGame,
}: StartSceneProps) {
  return (
    <SceneContainer _display={doDisplay ? 'grid' : 'none'}>
      <Header headerImage={headerImage} />
      <Center>
        <TextContainer>
          <Title>{title}</Title>
          <DescriptionText>{startText}</DescriptionText>
        </TextContainer>
      </Center>
      <Footer>
        <StartButton
          href=""
          onClick={(e: React.MouseEvent<HTMLElement>) => {
            e.preventDefault()
            startGame()
          }}
        >
          Start
        </StartButton>
      </Footer>
    </SceneContainer>
  )
}

export default StartScene

