import React, { useEffect, useState, useRef } from 'react'
import styled from 'styled-components'
import { Card as CardModel, Scene as SceneModel } from '../models/Elements'
import parse from 'html-react-parser'
import CancelSVG from '../media/cancel.svg'
import ScreenSize from '../models/ScreenSize'

interface CardContainerProps {
  top: number
  left: number
  width: number
  height: number
  stackingLevel: number
}

interface CardActionBoxSize {
  width: number
  height: number
}

export const CardContainer = styled.div<CardContainerProps>`
  width: ${(props) => props.width}px;
  height: ${(props) => props.height}px;
  top: ${(props) => props.top}px;
  left: ${(props) => props.left}px;
  color: black;
  display: grid;
  grid-template-rows: 1fr 10fr;
  grid-auto-flow: row;
  grid-auto-rows: 1fr;
  background: rgb(239, 211, 11);
  border-radius: 4px;
  border: 1.6px solid black;
  position: absolute;
  z-index: ${(props) => props.stackingLevel};
`

const CardActionBox = styled.div<CardActionBoxSize>`
  background-color: rgb(239, 211, 11);
  width: ${(props) => props.width}px;
  height: ${(props) => props.height}px;
`

const CardControlBox = styled.div`
  float: right;
`

const ControlIcon = styled.div`
  display: flex;
  padding: 0px 4px;
`
export const ContentBox = styled.div`
  padding: 0px 10px;
  overflow: auto;
  word-break: berak-word;
  overflow-wrap: break-word;
`

const BottomArea = styled.div`
  cursor: pointer;
  text-align: center;
  background-color: rgb(196, 195, 195);
  text-transform: uppercase;
  border-top: 2.4px solid black;
  & > p {
    margin: 0.5rem;
  }
`
3
export const TopArea = styled.div`
  text-align: center;
  width: 100%;
  border-bottom: 2.4px solid black;
  display: grid;
  grid-template-columns: 10fr 1fr;
  align-items: center;
  & > p {
    margin: 0.5rem;
  }
`

interface CardProps {
  scene: SceneModel
  card: CardModel
  isOpen: boolean
  gameSize: ScreenSize
  parent: React.RefObject<HTMLDivElement>
  hasCndAndFulfilled: boolean
  setCardState: (setOpen: boolean) => void
  changeToScene: (sceneUid: number) => void
  addCollectedCard: (cardUid: string) => void
  endGame: () => void
}

function Card({
  scene,
  card,
  isOpen,
  parent,
  gameSize,
  hasCndAndFulfilled,
  setCardState,
  changeToScene,
  addCollectedCard,
  endGame,
}: CardProps) {
  const childRef = useRef<HTMLDivElement>(null)
  const [childTop, setChildTop] = useState<number>(card.y)
  const [childLeft, setChildLeft] = useState<number>(card.x)

  const cardUid = `${scene.uid}-${card.uid}`

  const heightScalFact = gameSize.height / 550.0
  const widthScalFact = gameSize.width / 1000.0

  const squareWidth = Math.max(16, 18 * heightScalFact * widthScalFact)
  const squareHeight = Math.max(16, 18 * heightScalFact * widthScalFact)

  const cardWidth = 170 * widthScalFact
  const cardHeight = 220 * heightScalFact

  const handleChildClick = (
    c: React.RefObject<HTMLDivElement>,
    parent: React.RefObject<HTMLDivElement>,
  ) => {
    if (parent.current && c.current) {
      const parentRect = parent.current?.getBoundingClientRect()
      const childRect = c.current?.getBoundingClientRect()
      debugger // eslint-disable-line no-debugger
      const aimedBottomLoc = childRect.bottom + cardHeight - squareHeight
      const aimedRightLoc = childRect.right + cardWidth - squareWidth

      if (aimedBottomLoc > parentRect.bottom) {
        const diff = aimedBottomLoc + 10 - parentRect.bottom
        const newTop = childTop - diff
        setChildTop(newTop)
      }

      if (aimedRightLoc > parentRect.right) {
        const diff = aimedRightLoc + 10 - parentRect.right
        setChildLeft(childLeft - diff)
      }
    }
  }

  return (
    <CardContainer
      top={
        isOpen
          ? childTop * (gameSize.height / 550.0)
          : card.y * (gameSize.height / 550.0)
      }
      left={
        isOpen
          ? childLeft * (gameSize.width / 1000.0)
          : card.x * (gameSize.width / 1000.0)
      }
      width={isOpen ? cardWidth : squareWidth}
      height={isOpen ? cardHeight : squareHeight}
      stackingLevel={isOpen ? 30 : 0}
      ref={childRef}
    >
      {isOpen ? (
        <React.Fragment>
          <TopArea>
            <p>{card.title}</p>
            <CardControlBox>
              <ControlIcon onClick={() => setCardState(false)}>
                <img src={CancelSVG} style={{ height: 18, width: 18 }} />
              </ControlIcon>
            </CardControlBox>
          </TopArea>
          <ContentBox>
            <p>
              {card.requiredCard === -1 || !hasCndAndFulfilled
                ? parse(card.initialText)
                : hasCndAndFulfilled
                ? parse(card.advancingText)
                : null}
            </p>
          </ContentBox>

          {card.requiredCard === -1 || hasCndAndFulfilled ? (
            card.doEndWithLink ? (
              <BottomArea
                onClick={() => {
                  endGame()
                }}
              >
                <p>Weiter</p>
              </BottomArea>
            ) : card.doReceiveCard && card.sceneLink !== -1 ? (
              <BottomArea
                onClick={() => {
                  addCollectedCard(cardUid)
                  changeToScene(card.sceneLink)
                }}
              >
                <p>Mitnehmen und weiter</p>
              </BottomArea>
            ) : card.doReceiveCard ? (
              <BottomArea
                onClick={() => {
                  addCollectedCard(cardUid)
                }}
              >
                <p>Mitnehmen</p>
              </BottomArea>
            ) : (
              card.sceneLink !== -1 && (
                <BottomArea
                  onClick={() => {
                    changeToScene(card.sceneLink)
                  }}
                >
                  <p>Weiter</p>
                </BottomArea>
              )
            )
          ) : null}
        </React.Fragment>
      ) : (
        <CardActionBox
          width={squareWidth}
          height={squareHeight}
          onClick={() => {
            setCardState(true)
            handleChildClick(childRef, parent)
          }}
        />
      )}
    </CardContainer>
  )
}

export default Card
