import React, { useEffect, useState, useRef } from 'react'
import styled from 'styled-components'
import { Scene as SceneModel, Card as CardModel } from '../models/Elements'
import Card from './Card'
import SceneLink from './SceneLink'
import ScreenSize from '../models/ScreenSize'

interface SceneProps {
  scene: SceneModel
  collectedCards: Array<string>
  doDisplay: boolean
  gameSize: ScreenSize
  changeToScene: (sceneUid: number) => void
  addCollectedCard: (cardUid: string) => void
  endGame: () => void
}

interface SceneContainerProps {
  _display: string
  backgroundPath: string
}
const SceneContainer = styled.div<SceneContainerProps>`
  margin: 0 auto;
  height: 100%;
  display: ${(props) => props._display};
  width: inherit;
  z-index: 10;
  background-size: 100% 100%;
  background-image: url(${(props) => props.backgroundPath});
`

function Scene({
  scene,
  collectedCards,
  doDisplay,
  gameSize,
  changeToScene,
  addCollectedCard,
  endGame,
}: SceneProps) {
  const parentRef = useRef<HTMLDivElement>(null)
  const [cardIdToOpenState, setCardIdToOpenState] = useState<
    Map<number, boolean>
  >(new Map())

  useEffect(() => {
    const initMapData: [number, boolean][] = scene.cards.map((card) => [
      parseInt(card.uid),
      false,
    ])
    setCardIdToOpenState(new Map<number, boolean>(initMapData))
  }, [])

  function setCardOpen(cardUid: number, setOpen: boolean) {
    if (cardIdToOpenState.has(cardUid)) {
      const updatedMap = new Map<number, boolean>(cardIdToOpenState)
      updatedMap.forEach((_value, key) => {
        updatedMap.set(key, cardUid == key ? setOpen : false)
      })
      setCardIdToOpenState(updatedMap)
    }
  }

  return (
    <SceneContainer
      ref={parentRef}
      _display={doDisplay ? 'block' : 'none'}
      backgroundPath={scene.backgroundPath}
    >
      {scene.sceneLinks.map((sl) => {
        const actionUid = `${scene.uid}-${sl.uid}`
        return (
          <SceneLink
            key={actionUid}
            scene={scene}
            action={sl}
            changeToScene={changeToScene}
          />
        )
      })}
      {scene.cards.map((c) => {
        const curCardUid = `${scene.uid}-${c.uid}`
        return !collectedCards.includes(curCardUid) ? (
          <Card
            key={curCardUid}
            parent={parentRef}
            scene={scene}
            isOpen={cardIdToOpenState.get(parseInt(c.uid)) as boolean}
            gameSize={gameSize}
            setCardState={(setOpen: boolean) => {
              setCardOpen(parseInt(c.uid), setOpen)
            }}
            card={c}
            hasCndAndFulfilled={
              c.requiredCard !== -1 &&
              collectedCards.includes(c.requiredCard as string)
            }
            changeToScene={(sceneUid: number) => {
              const updatedMap = new Map<number, boolean>()
              cardIdToOpenState.forEach((_value, key) => {
                updatedMap.set(key, false)
              })
              setCardIdToOpenState(updatedMap)
              changeToScene(sceneUid)
            }}
            addCollectedCard={addCollectedCard}
            endGame={endGame}
          />
        ) : null
      })}
    </SceneContainer>
  )
}

export default Scene
