import React, { useEffect, useState, useRef } from 'react'
import cardVisibilityLogo from './media/card_visibility.png'

import { sampleConf, metadata as metadataConf } from './misc/Constants'
import { ContentBox, TopArea } from './components/Card'
import { Metadata } from './models/Metadata'
import {
  Card,
  Action,
  Scene as SceneModel,
  SceneUtils,
  MetaScene,
} from './models/Elements'
import GlobalStyle from './theme/globalStyles'
import Scene from './components/Scene'
import StartScene from './components/StartScene'
import EndScene from './components/EndScene'
import CardInspection from './components/CardInspection'
import { range } from 'lodash'
import styled from 'styled-components'
import { truncate } from 'fs'
import ScreenSize from './models/ScreenSize'

const BASE_URL = process.env.REACT_APP_BASE_URL

const cardVisualContainerWidth = 150.0
const cardVisualWidth = 110.0
const cardVisualHeight = 150.0

interface DimProps {
  width: string
  height: string
}

const MainContainer = styled.div<DimProps>`
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
`

const ContentContainer = styled.div<DimProps>`
  position: relative;
  width: ${(props) => props.width};
  height: ${(props) => props.height};
`

const CardVisualsOuterContainer = styled.div<StackingContextProps>`
  height: 35%;
  position: absolute;
  z-index: ${(props) => props.stackingContext};
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
`

interface CardVisualInnerContainer {
  width: number
}

const CardVisualsInnerContainer = styled.div`
  width: ${cardVisualContainerWidth}px;
  display: flex;
  justify-content: center;
  position: relative;
`

interface CardVisualProps {
  transform: string
  left: number
  hoverTransform: string
  opacity: number
  additional: string
}

const CardVisual = styled.div<CardVisualProps>`
  width: ${cardVisualWidth}px;
  height: ${cardVisualHeight}px;
  background: rgb(239, 211, 11);
  border-radius: 4px;
  border: 1.6px solid black;
  overflow: auto;
  text-align: center;
  z-index: 13;
  position: absolute;
  opacity: ${(props) => props.opacity};
  transition: all 0.8s 0s ease;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
  transform: ${(props) => props.transform};
  left: ${(props) => props.left}px;
  ${(props) => props.additional}
  &:hover {
    transform: ${(props) => props.hoverTransform};
  }
`
const CardVisualContent = styled.div`
  padding: 0px 10px;
`

const VisualInspectionContainer = styled.div<DimProps>`
  position: absolute;
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  z-index: 15;
  align-items: center;
`

interface StackingContextProps {
  stackingContext: number
}

const VisualInspectionGridWrapper = styled.div`
  height: 100%;
  width: 100%;
  background-color: rgba(32, 32, 32, 0.6);
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  place-items: center;
  z-index: 30;
`

const Arrow = styled.i`
  border: solid white;
  border-width: 0 10px 10px 0;
  display: inline-block;
  padding: 10px;
`

const RightArrow = styled(Arrow)`
  transform: rotate(-45deg);
`
const LeftArrow = styled(Arrow)`
  transform: rotate(135deg);
`
interface CardVisibilityLogoContainerProps {
  opacity: number
}

const CardVisibilityLogoContainer = styled.div<CardVisibilityLogoContainerProps>`
  position: absolute;
  z-index: 30;
  bottom: 20px;
  left: 20px;
  opacity: ${(props) => props.opacity};
`

interface TWindow extends Window {
  gameConfig: string
  metadata: Metadata
}

// debugger // eslint-disable-line no-debugger

const tWindow = window as unknown as TWindow
const _scenes =
  !process.env.NODE_ENV || process.env.NODE_ENV === 'development'
    ? SceneUtils.of(sampleConf)
    : SceneUtils.of(JSON.parse(tWindow.gameConfig))
const initialCollectedCards: Array<string> = []

const metadata: Metadata =
  !process.env.NODE_ENV || process.env.NODE_ENV === 'development'
    ? metadataConf
    : tWindow.metadata
const rotAngleRange = 30

function App() {
  const [collectedCards, setCollectedCards] = useState(initialCollectedCards)
  const [currentScene, setCurrentScene] = useState<SceneModel>(_scenes[0])
  const [scenes, setScenes] = useState(_scenes)
  const [cardInspectionActive, setCardInspectionActive] = useState(false)
  const [currentInspectedCard, setCurrentInspectedCard] = useState<Card | null>(
    null,
  )
  const [isFullscreen, setIsFullscreen] = React.useState(false)
  const [doHideCards, setDoHideCards] = React.useState(true)
  const [isGameStart, setIsGameStart] = React.useState(
    Object.hasOwn(process.env, 'REACT_APP_SKIP_START_SCENE') &&
      !!process.env.REACT_APP_SKIP_START_SCENE
      ? false
      : true,
  )
  const [isGameOver, setIsGameOver] = React.useState(false)
  const [isEnlarged, setIsEnlarged] = React.useState(false)
  const [screenSize, setScreenSize] = useState<ScreenSize>({
    width: window.innerWidth,
    height: window.innerHeight,
  })

  /// Watch for fullscreenchange
  React.useEffect(() => {
    const handleResize = () => {
      setScreenSize({ width: window.innerWidth, height: window.innerHeight })
    }

    window.addEventListener('resize', handleResize)

    return () => {
      window.removeEventListener('resize', handleResize)
    }
  }, [])

  function addCollectedCard(cardUid: string) {
    setCollectedCards([cardUid, ...collectedCards])
    setDoHideCards(false)
  }

  function startGame() {
    setIsGameStart(false)
    changeToScene(0)
  }

  function endGame() {
    setCardInspectionActive(false)
    setIsGameOver(true)
  }

  function changeToScene(sceneUid: number) {
    //document.body.requestFullscreen()
    setCurrentScene(scenes.find((s: SceneModel) => sceneUid === s.uid))
  }
  // debugger // eslint-disable-line no-debugger

  const noCollectableCards = scenes
    .flatMap((s: SceneModel) => s.cards)
    .filter((c: Card) => c.doReceiveCard).length

  const gHeight: number =
    screenSize.height <= 550 || screenSize.width <= 1000
      ? Math.min(screenSize.height, screenSize.width / 1.8181)
      : 550
  const gWidth: number =
    screenSize.width <= 1000 || screenSize.height <= 550
      ? Math.min(screenSize.width, screenSize.height * 1.8181)
      : 1000

  const logoWidth: number = Math.max(80, 100 * (gWidth / 1000.0))
  const gHeightPx = `${gHeight}px`
  const gWidthPx = `${gWidth}px`
  return (
    <React.Fragment>
      <GlobalStyle />
      {/*<input type="checkbox" onChange={() => setIsEnlarged(!isEnlarged)} />*/}

      <MainContainer height={gHeightPx} width={gWidthPx}>
        {cardInspectionActive && (
          <VisualInspectionContainer height={gHeightPx} width={gWidthPx}>
            <VisualInspectionGridWrapper
              onClick={() => setCardInspectionActive(false)}
            >
              <div></div>
              <LeftArrow
                onClick={(e) => {
                  e.stopPropagation()
                  const cCards: Array<Card> = getCards()
                  const leftIdx: number =
                    cCards.indexOf(currentInspectedCard as Card) - 1
                  setCurrentInspectedCard(
                    leftIdx < 0 ? cCards[cCards.length - 1] : cCards[leftIdx],
                  )
                }}
              />
              <CardInspection card={currentInspectedCard as Card} />
              <RightArrow
                onClick={(e) => {
                  e.stopPropagation()

                  const cCards: Array<Card> = getCards()

                  console.log(
                    (cCards.indexOf(currentInspectedCard as Card) + 1) %
                      cCards.length,
                  )
                  setCurrentInspectedCard(
                    cCards[
                      (cCards.indexOf(currentInspectedCard as Card) + 1) %
                        cCards.length
                    ],
                  )
                }}
              />
              <div></div>
            </VisualInspectionGridWrapper>
          </VisualInspectionContainer>
        )}
        <ContentContainer height={gHeightPx} width={gWidthPx}>
          {!isGameStart &&
            !isGameOver &&
            !cardInspectionActive &&
            collectedCards.length > 0 && (
              <CardVisibilityLogoContainer
                opacity={1}
                onClick={() => {
                  /*setDoHideCards(!doHideCards)*/
                  setCurrentInspectedCard(getCards()[0])
                  setCardInspectionActive(true)
                }}
              >
                <img width={logoWidth} src={cardVisibilityLogo} alt="icon" />
              </CardVisibilityLogoContainer>
            )}
          <StartScene
            headerImage={metadata.headerImage}
            title={metadata.title}
            startText={metadata.startText}
            doDisplay={isGameStart}
            startGame={startGame}
          />
          <EndScene
            headerImage={metadata.headerImage}
            title={metadata.title}
            endText={metadata.endText}
            doDisplay={isGameOver}
          />

          {!isGameStart &&
            !isGameOver &&
            scenes.map((s: SceneModel) => {
              return (
                <Scene
                  key={s.uid}
                  scene={s}
                  doDisplay={s.uid === currentScene.uid}
                  collectedCards={collectedCards}
                  gameSize={{ width: gWidth, height: gHeight }}
                  changeToScene={changeToScene}
                  addCollectedCard={addCollectedCard}
                  endGame={endGame}
                />
              )
            })}
          {false && !isGameStart && !isGameOver && !cardInspectionActive && (
            <CardVisualsOuterContainer stackingContext={doHideCards ? 11 : 22}>
              <CardVisualsInnerContainer>
                {scenes
                  .flatMap((s: SceneModel) =>
                    s.cards.filter((c: Card) =>
                      collectedCards.includes(`${s.uid}-${c.uid}`),
                    ),
                  )
                  .map((c: Card, idx: number) => {
                    const rotAngleStep: number =
                      rotAngleRange / (noCollectableCards - 1) // Distribute 30 degree rotation between all the cards
                    const leftStepSize =
                      cardVisualContainerWidth / (noCollectableCards - 1)
                    return (
                      <CardVisual
                        onClick={() => {
                          if (doHideCards) return
                          setCurrentInspectedCard(c)
                          setCardInspectionActive(true)
                        }}
                        key={`${idx}`}
                        additional={doHideCards ? 'pointer-events: none;' : ''}
                        opacity={doHideCards ? 0.1 : 1.0}
                        transform={`rotate(${
                          collectedCards.length == 1
                            ? 0
                            : -rotAngleRange / 2.0 + idx * rotAngleStep
                        }deg) translateY(${
                          -rotAngleRange / 2.0 + (idx * rotAngleStep) / 3
                        }px)`}
                        left={
                          collectedCards.length == 1
                            ? 0
                            : idx * leftStepSize +
                              leftStepSize / 2.0 -
                              cardVisualHeight / 2.0
                        }
                        hoverTransform={`rotate(${
                          -rotAngleRange / 2.0 + idx * rotAngleStep
                        }deg) scale(1.05)`}
                      >
                        <CardVisualContent>
                          {/*<h3>{c.title}</h3>*/}
                        </CardVisualContent>
                      </CardVisual>
                    )
                  })}
              </CardVisualsInnerContainer>
            </CardVisualsOuterContainer>
          )}
        </ContentContainer>
      </MainContainer>
    </React.Fragment>
  )

  function getCards() {
    return scenes.flatMap((s: SceneModel) =>
      s.cards.filter((c: Card) => collectedCards.includes(`${s.uid}-${c.uid}`)),
    )
  }
}

export default App
